.detail-banner {
    /* width: 75vw; */
    font-size: 0.9em;
    border-radius: 0.5em;
    font-family: 'Open Sans', sans-serif;    
    margin-bottom: 1em;
}
.detail-top {
    display: flex;
    justify-content: space-around;
    /* background-color: rgba(224, 218, 218, 0.295); */
    margin: 3em auto;
    padding: 3em auto;
    width: 80vw;
    /* height: 80vh; */
    border-radius: 0.5em;
  }
.detail-home {
    position: relative;
    display: flex;
    flex-flow: column;
    align-items: center;
    margin-top: 1.5em;
    width: 45em;
    height: 15em;
    font-size: 1.3em;
    line-height: 1.2;
    border-radius: 0.5em;
    padding: 0.5em;
    /* text-align: justify; */
    color: rgb(66, 52, 52);
    background-color: rgba(224, 218, 218, 0.295);
    /* color: rgb(223, 207, 207); */
    box-shadow: 0.2em 0.3em 0.2em rgb(11, 12, 12);
}

.title-home {
    font-size: 1.3em;
    font-weight: 700;
    color: #6c6c74;
    box-shadow: none;
    margin-bottom: 0.5em;
}
.slide-home {
    font-size: 1em;
    font-weight: bolder;
    color: #1f1f22;
    box-shadow: none;
    margin-bottom: 1em;
}
.close-btn {
    align-self: flex-end;
    margin: 0 1em -2em -1em;
}
.close-btn:hover {
    cursor: pointer;
    color: rgb(75, 67, 67);
    /* background-color: #000; */
}
.details {
    display: flex;
    flex-flow: column;
    align-items: flex-start;
}
.details span {
    margin-bottom: 1em;
}